<script>
export default {
  name: "ShopThankYou",
  data: () => ({
    orderCode: null,
    orderDetails: [],
    order_amount:0,
    shipping_address:{}
  }),
  created() {
    this.orderCode = this.$route.query["order-code"];
    if (!this.orderCode) {
      window.location.replace("/");
    }
    this.$http.get(`ghost/shop/orders/details/${this.orderCode}`)
      .then((response) => {
        this.orderDetails = response.data.details;
        this.order_amount = response.data.order_amount;
        this.shipping_address = response.data.shipping_address;
      })
      .catch((err) => {
        if (err.response.status == 404) {
          this.dispatch("logout").then(() => {
            window.location.replace("/");
          });
        }
      });
  },
};
</script>
