<script>
import countries from "@/_helpers/countries";
import Ticketing from "./Ticketing.vue";
import Membership from "./Membership.vue";
export default {
  components: { Ticketing, Membership },
  data: () => ({
    countries: countries,
    currentService: "MembershipCard",
    card: {},
    grandTotal: 0,
    newPayment: {
      name: null,
      country: "Rwanda",
      email: null,
      phone_number: null,
      terms_agreed: 1,
      address: {
        address_one: null,
        country: "Rwanda",
        city: null,
        address_two: null,
        zip_code: null,
        state: null,
        company: null,
      },
    },
    paymentMethod: "momo",
    processing: false,
    errorsMessages: {},
    paymentMeta: {},
    paymentModal: false,
    paymentStatusInterval: null,
    paymentCancelled: false,
    submitButtonText: "Subscribe",
    selectedTicket: {},
    match: {},
  }),
  computed: {
    validationErrors() {
      let errors = Object.values(this.errorsMessages);
      errors = errors.flat();
      return errors;
    },
    btnDisabled() {
      return (
        this.$store.state.requestFlag == "processingPayment" ||
        !this.newPayment.name ||
        !this.newPayment.phone_number
      );
    },
  },
  created() {
    if (this.$route.query.service == "ticketing") {
      this.currentService = "Ticketing";
      let query = this.$route.query;
      this.selectedTicket = {
        qty: query.qty,
        reference: query.s_ref,
        slug: query.slug,
      };
      this.$http
        .get(`ghost/tickets/single/${query.slug}/${query.s_ref}`)
        .then((response) => {
          if (!response.data.ticket) {
            window.location.replace("/");
          }
          this.match = response.data.match;
          this.selectedTicket = {
            ...this.selectedTicket,
            ...response.data.ticket,
          };
          this.submitButtonText = `Pay  RWF ${this.addComma(
            this.selectedTicket.price * this.selectedTicket.qty
          )}`;
        });
    } else {
      let code = this.$route.query.s_ref;
      this.$http.get(`ghost/membership-cards/${code}`).then((response) => {
        if (!response.data.card) {
          window.location.replace("/");
        }
        this.card = response.data.card;
      });
    }
  },
  methods: {
    delegatePaymentAction() {
      this.$validator.validateAll().then((result) => {
        if (result) {
          var formData, url;
          if (this.currentService == "Ticketing") {
            formData = this.formData({
              ...this.selectedTicket,
              ...this.newPayment,
            });
            url = "ghost/purchase/tickets";
          } else {
            url = "cullers/memberships/purchase";
            formData = this.formData({
              ...{ code: this.$route.query.s_ref },
              ...this.newPayment,
            });
          }
          this.$store.state.requestFlag = "processingPayment";
          this.processing = true;
          this.paymentModal = false;
          this.paymentCancelled = false;
          this.$http
            .post(url, formData)
            .then((response) => {
              if (response.data.status) {
                this.paymentMeta = response.data.payment;
                this.processing = false;
                if (this.paymentMeta.success == 1) {
                  if (
                    this.paymentMethod == "momo" ||
                    this.paymentMethod == "spenn"
                  ) {
                    if (this.paymentMeta.reply == "PENDING") {
                      this.handlePendingPayment({
                        transaction_ref: this.paymentMeta.refid,
                        authkey: this.paymentMeta.authkey,
                        tid: this.paymentMeta.tid,
                      });
                    }
                    this.paymentModal = true;
                  } else {
                    window.location.replace(response.data.payment.url);
                  }
                } else {
                  this.paymentModal = true;
                  this.paymentCancelled = true;
                }
              } else {
                this.errorsMessages = response.data.message;
              }
            })
            .catch(() => {
              this.$store.state.requestFlag = null;
            });
        }
      });
    },
    handlePendingPayment(obj) {
      if (this.paymentStatusInterval) {
        clearInterval(this.paymentStatusInterval);
      }
      this.$store.state.requestFlag = "fetchingStatus";
      let url = "payment-status";
      let formData = this.formData(obj);
      this.$http
        .post(url, formData)
        .then((response) => {
          if (response.data.transaction) {
            var status = response.data.transaction.status;
            if (status != "successful" && status != "failed") {
              this.paymentStatusInterval = setInterval(() => {
                this.$store.state.requestFlag = "fetchingStatus";
                this.$http.post(url, formData).then((response) => {
                  if (response.data.transaction.status == "successful") {
                    clearInterval(this.paymentStatusInterval);
                    this.$router.replace({
                      name: "PublicDownloads",
                      params: {},
                      query: { origin: "payment", thankyou: true },
                    });
                  }
                  if (response.data.transaction.status == "failed") {
                    clearInterval(this.paymentStatusInterval);
                    this.paymentCancelled = true;
                  }
                });
              }, 4000);
            }
          }
        })
        .catch(() => {
          this.paymentCancelled = true;
        });
    },
    handlePaymentToggler(payment) {
      this.paymentMethod = payment;
    },
  },
};
</script>
